import './download.less'
import Icon from '@/component/icon'
import { download } from '@/utils/getOsInfo'
import getLan from '@/utils/getLan'
const lanType = getLan()
const lan = require(`@/language/${lanType}/home`)

export default function Download() {
    const appList = [
        {
            title: "WINDOWS",
            icon: "iconwindows",
            os:"windows"
        },
        {
            title: "LINUX",
            icon: "iconlunix",
            os: "linux"
        },
        {
            title: "MACOS",
            icon: "iconmac",
            os: "mac"
        },
    ]
    return (
        <div className="download-box">
            <div className="title-box">{lan.nav.download}</div>
            <div className="content-box">
                <div className="list-box">
                    {
                        appList.map((item, index) => (
                            <div key={index} className="list-content"
                                onClick={
                                    () => {
                                        download(item.os)
                                    }
                                }
                            >
                                <div className="icon-box">
                                    <Icon  type={item.icon} style={{ fontSize: 88, color: "#2D324F" }}></Icon>
                                </div>
                                <div className="btn-box">
                                    <div className="title">
                                        {item.title}
                                        <Icon  type="iconxiazai1" style={{ fontSize: '.16rem', color: "#FFFFFF", paddingLeft: '.1rem' }}></Icon>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}