
import getLan from '@/utils/getLan'
import Home from '@/pages/home'

const lanType = getLan()
const lan = require(`../language/${lanType}/home`)

const routes = [
    {
        title: lan.nav.home,
        path: "/",
        exact: true,
        component: () => <Home></Home>,
    },
    {
        title: lan.nav.docs,
        path: "/docs",
        component: () => <div>Docs</div>,
    },
    {
        title: lan.nav.updates,
        path: "/updates",
        component: () => <div>Updates</div>,
    },
    {
        title: lan.nav.about,
        path: "/about",
        component: () => <div>About</div>,
    },
];


export default routes