import {
  Link
} from "react-router-dom";
import routes from '@/router'
import './nav.less'
import getLan from '@/utils/getLan'
const lanType = getLan()
const lan = require(`@/language/${lanType}/home`)

//导航
export function NavList() {
  return (
    <div className="nav-ul-box">

      {

        routes.map((route, index) => (
          <div
            key={index}
            className="li-box"
          >
            <Link to={route.path}>{route.title}</Link>
          </div>
        ))

      }
      <div className="li-box">
        {<a href="mailto:feedback@reversec.cn">{lan.nav.feedback}</a>}
      </div>
    </div>
  )
}


//header头部
function Nav() {
  return (
    <div className="nav-box">
      <div className="content-box">
        <div className="nav-list-box">
          <img src={'/incilogo.svg'} alt="incinerator" />
          <NavList></NavList>
        </div>
      </div>
      <div 
        onClick={
          () => {
            window.scrollTo(0,document.body.scrollHeight)
          }
        }
        className="nav-download-box">{lan.nav.download}</div>
    </div>
  )
}


export default Nav