import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import './utils/rem' //动态计算rem单位
import './App.css';

import routes from './router'
import Nav from './pages/layout/nav/nav.js'
import Bottom from './pages/layout/bottom/bottom.js'


function App() {
  return (
    <Router>
      <div>
        {/* 导航栏 */}
        <Nav></Nav>

        {/* 内容 */}
        <Switch>
          {routes.map((route, index) => (
            <Route
              key={index}
              path={route.path}
              exact={route.exact}
              children={<route.component />}
            />
          ))}
        </Switch>

        {/* 底部导航 */}
        <Bottom></Bottom>

      </div>
    </Router>
  );
}

export default App;
